import React from "react";
import { motion } from "framer-motion";
import SplitText from "../blocks/TextAnimations/SplitText/SplitText";

const Hero = () => {
  // Animation variants
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1,
        delayChildren: 0.3
      }
    }
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: {
        duration: 0.6,
        ease: [0.16, 1, 0.3, 1]
      }
    }
  };

  const buttonVariants = {
    hover: {
      y: -3,
      transition: {
        duration: 0.2,
        ease: "easeOut"
      }
    },
    tap: {
      scale: 0.95
    }
  };

  return (
    <section id="hero" className="hero-section">
      <div className="background-overlay"></div>
      
      <motion.div 
        className="hero-content"
        initial="hidden"
        animate="visible"
        variants={containerVariants}
      >
        <motion.div className="hero-text" variants={containerVariants}>
          <h1 className="hero-title">
            <SplitText text="Hi, I'm Selva" animation="fadeIn" delay={50} />
          </h1>
          
          <motion.p 
            className="hero-tagline"
            variants={itemVariants}
            style={{ animationDelay: "0.3s" }}
          >
            Data Analyst | Insight-Driven Storyteller
          </motion.p>
          
          <motion.p 
            className="hero-description"
            variants={itemVariants}
            style={{ animationDelay: "0.6s" }}
          >
            Transforming complex data into actionable insights with <span className="highlight-text">Python</span>,{" "}
            <span className="highlight-text">Power BI</span>, and{" "}
            <span className="highlight-text">Data Storytelling</span> for business growth.
          </motion.p>
        </motion.div>

        <motion.div 
          className="cta-buttons"
          variants={itemVariants}
        >
          <motion.a 
            href="#portfolio" 
            className="hero-btn primary"
            variants={buttonVariants}
            whileHover="hover"
            whileTap="tap"
          >
            View Portfolio
          </motion.a>
          
          <motion.a 
            href="#contact" 
            className="hero-btn secondary"
            variants={buttonVariants}
            whileHover="hover"
            whileTap="tap"
          >
            Contact Me
          </motion.a>
        </motion.div>
      </motion.div>
    </section>
  );
};

export default Hero;